@import "vars";

.secConfirm01 {

    >.box {
        width: 900px;
        margin: auto;

        @include mq(md) {
            width: initial;
            // padding: 0 15px;
        }



        >.tab {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            margin-bottom: 58px;

            @include mq(md) {
                margin-bottom: 30px;

            }

            >.item {
                height: 41px;
                border-bottom: 6px solid #F0F0F0;

                >.title {
                    font-size: 20px;
                    font-weight: 600;
                    line-height: 34px;
                    color: #D4D4D4;
                    text-align: center;

                }

                >.titleActive {
                    color: #080808;

                }
            }

            >.itemActive {
                border-bottom: 6px solid #356FBF;

            }
        }

        >.form {
            margin-bottom: 59px;

            @include mq(md) {
                margin-bottom: 50px;
            }

            >.item {
                padding: 0 10px;
                border-bottom: 1px solid #979797;
                min-height: 84px;
                display: flex;

                @include mq(md) {
                    padding: 20px 0;
                    height: initial;
                }

                >.grid {
                    display: flex;
                    justify-content: space-between;
                    height: min-content;
                    margin: auto 0;
                    width: 100%;

                    @include mq(md) {
                        display: flex;
                        justify-content: space-between;
                        flex-wrap: wrap;
                    }

                    >.col01 {
                        width: 100%;
                        display: flex;

                        @include mq(md) {
                            height: 40px;
                            margin-bottom: 20px;
                        }

                        >.title {
                            font-size: 16px;
                            font-weight: 600;
                            line-height: 30px;
                            margin: auto;
                            margin-left: 0;
                        }

                        >.title01 {
                            font-size: 16px;
                            font-weight: 600;
                            line-height: 30px;
                            margin-top: 27px;
                            white-space: pre;

                            @include mq(md) {
                                margin-top: 15px;
                                white-space: initial;

                            }
                        }

                    }

                    >.col02 {
                        min-width: 590px;
                        width: 590px;
                        display: flex;
                        position: relative;
                        margin: auto;

                        @include mq(md) {
                            min-width: initial;
                            width: 100%;
                        }

                        >.txt {
                            font-size: 16px;
                            line-height: 30px;
                            color: #080808;
                            display: flex;
                            align-items: center;
                            line-height: initial;
                            margin-top: 30px;
                            margin-bottom: 30px;

                            @include mq(md) {
                                margin: auto;

                            }
                        }
                    }
                }

            }

            >.btn {
                margin: auto;
            }
        }

        >.list {
            display: flex;
            flex-wrap: wrap;
            gap: 30px;
            justify-content: center;

            >.item {
                @include mq(md) {
                    width: 100%;
                    display: flex;
                    justify-content: center;
                }
            }
        }


    }

    &aa {
        padding-top: 31px;
        padding-bottom: 98px;

        @include mq(md) {
            padding: 0 15px;
            padding-top: 60px;
            padding-bottom: 80px;
        }
    }
}